<template>
<section class="content-header">
    <ol class="breadcrumb">
        <li><a href="">Tizi.vn</a></li>
        <li><a href="#">{{ getProvinceByUrl(province) ? getProvinceByUrl(province).name : '' }}</a></li>
        <li v-if="!(province=='toan-quoc')"><a href="#">{{ getProvinceByUrl(district) ? getProvinceByUrl(district).name : '' }}</a></li>
        <li class="active">{{ getCategoryByUrl(category) ? getCategoryByUrl(category).name : '' }}</li>
    </ol>
    <h1>{{ getCategoryByUrl(category) ? getCategoryByUrl(category).title : '' }} tại {{ getProvinceByUrl(province) ? getProvinceByUrl(province).name : '' }} <span v-if="!(province=='toan-quoc')">- {{ getProvinceByUrl(district) ? getProvinceByUrl(district).name : '' }}</span></h1>
</section>
</template>

<script>
import {mapGetters} from "vuex";

export default {
    data(){
        return {
            province: this.$route.params.province,
            district: this.$route.params.district,
            category: this.$route.params.category
        }
    },
    computed:{
        ...mapGetters([
            'getProvinceByUrl',
            'getCategoryByUrl'
        ])
    },
}
</script>