<template>
    <div class="content-body">
        <div class="classify">
            <div class="nav-tabs-custom">
                <ul class="nav nav-tabs">
                    <li :class="{active: sort.group=='tat-ca'}"><a v-on:click="group('tat-ca')" href="javascript:void(0)" data-toggle="tab" aria-expanded="true">Tất cả</a>
                    </li>
                    <li :class="{active: sort.group=='ca-nhan'}"><a v-on:click="group('ca-nhan')" href="javascript:void(0)" data-toggle="tab" aria-expanded="false">Cá nhân</a>
                    </li>
                    <li :class="{active: sort.group=='chuyen'}"><a v-on:click="group('chuyen')" href="javascript:void(0)" data-toggle="tab" aria-expanded="false">Chuyên</a>
                    </li>
                </ul>
                <div class="tab-content">
                    <div class="tab-pane active" id="tab_1">
                        <transition-group name="products" tag="div">
                            <div v-for="(item,index) in product" :key="index+1" :class="{item: true, priority: item.product_priority}">
                                <div class="row">
                                    <div class="col-lg-4 col-md-4 col-sm-5 col-xs-5">
                                        <router-link :to="{name:'route.product', params:{province: item.url_province, district: item.url_district, category: item.url_category, id: item.id, product: item.url }}" v-bind:title="item.name">
                                            <img class="img" v-bind:src="api + '/image/product/thumbnail/' + item.image" v-bind:alt="item.name">
                                        </router-link>
                                        <a v-if="$store.state.user.username != item.username" class="save-product">
                                            <span v-if="item.isProductFollow" class="save">Đã lưu</span>
                                            <span v-if="!item.isProductFollow" v-on:click="saveProduct(item.id)" class="save">Lưu tin</span>
                                        </a>
                                        <a v-else class="save-product">
                                            <span class="save">Tin của bạn</span>
                                        </a>
                                    </div>
                                    <div class="col-lg-8 col-md-8 col-sm-7 col-xs-7">
                                        <router-link :to="{name:'route.product', params:{province: item.url_province, district: item.url_district, category: item.url_category, id: item.id, product: item.url }}" v-bind:title="item.name">
                                            <h2 :class="{title: true, priority: item.product_priority}">{{ item.name }}</h2>
                                            <span class="product-type default cat">
                                                <i class="fa fa-fw fa-indent"></i> {{ item.category }}
                                            </span>
                                            <span v-if="item.product_priority" class="product-type">
                                                <i class="fa fa-star"></i>Hot
                                            </span>
                                            <div class="about">
                                                <span v-if="item.price!=0" class="price">{{ number_format(item.price, 0) }} đ</span>
                                                <span v-if="item.price==0" class="price">Thỏa thuận</span>
                                                <div>
                                                    <span v-if="!item.product_priority" class="product-time"><i class="fa fa-clock-o" aria-hidden="true"></i> {{ getTime(item.date) }}</span>
                                                    <span v-if="item.product_priority" class="product-time"><i class="fa fa-buysellads" aria-hidden="true"></i> Quảng cáo</span> 
                                                    <span class="product-user"><i :class="{fa:true, 'fa-archive': item.is_vip, 'fa-user': !item.is_vip}"></i> {{ item.username }}</span>
                                                    <span v-if="$route.params.province == 'toan-quoc'" class="product-address"><i class="fa fa-map-marker" aria-hidden="true"></i> {{ item.province }}</span>
                                                    <span v-if="$route.params.district == 'tat-ca-quan-huyen'" class="product-address"><i class="fa fa-map-marker" aria-hidden="true"></i> {{ item.district }}</span>
                                                    <span v-if="$route.params.district != 'tat-ca-quan-huyen' && $route.params.province != 'toan-quoc'" class="product-address"><i class="fa fa-map-marker" aria-hidden="true"></i> {{ item.address }}</span>
                                                </div>
                                            </div>
                                        </router-link>
                                    </div>
                                </div>
                            </div>
                        </transition-group>
                        <paginate v-model="sort.page"
                            :page-count="pageCount"
                            :page-range="3"
                            :margin-pages="2"                                           
                            :click-handler="runPage"
                            :prev-text="`<i class='fa fa-angle-left' aria-hidden='true'></i>`"
                            :next-text="`<i class='fa fa-angle-right' aria-hidden='true'></i>`"
                            :container-class="'pagination'"
                            :page-class="'page-item'">
                        </paginate>
                    </div>
                    
                </div>
            </div>
        </div>
    </div>	
</template>

<script>
import axios from 'axios';
import moment from "moment";
import Paginate from 'vuejs-paginate';

import {mapGetters} from "vuex";

export default {
    name: "Products",
    components: {Paginate},
    data() {
        return {
            api: this.$store.state.domainApi,
            pageCount: this.$store.state.pageCount,
            sort:{
                id_province: this.$route.params.province,
                id_district: this.$route.params.district ? this.$route.params.district : null,
                id_category: this.$route.params.category,
                search: this.$route.query.search ? this.$route.query.search : null,
                priceFrom: this.$route.query.priceFrom ? this.$route.query.priceFrom : null,
                priceTo: this.$route.query.priceTo ? this.$route.query.priceTo : null,
                sortOption: this.$route.query.sortOption ? this.$route.query.sortOption : "time-new",
                group: this.$route.query.group ? this.$route.query.group : 'tat-ca',
                page: this.$route.query.page ? Number(this.$route.query.page) : 1,
                pageSize: this.$store.state.pageSize
            },
            product: []
        }
    },
    computed: {
        ...mapGetters({productFollow: 'getProductFollow'})
    },
    methods: {
        saveProduct(id){
            if(this.$store.state.token){
                axios.post(this.api+"/public/product-follow",{
                    id_user: this.$store.state.user.id,
                    id_product: id
                })
                .then(response=>{
                    if(response.data.success){
                        this.flash(response.data.message, "success", { timeout: 3000 });
                        this.$store.state.user.productFollow.push({id_product:id});
                        this.$store.state.countProductFollow++;
                        this.product.forEach((e,index)=>{
                            if(e.id == id) this.product[index].isProductFollow = true;
                        })
                    }else this.flash(response.data.message, "error", { timeout: 3000 });
                })
            }else this.$swal.fire({
                title: 'Vui lòng đăng nhập !',
                icon: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                cancelButtonText: 'Thoát',
                confirmButtonText: 'Đăng nhập'
            }).then((result) => {
                if (result.value) this.$router.push({name: 'login'});
            });
        },
        runPage(page){
            this.sort.page = page;
            this.redirect();
        },
        number_format( number, decimals, dec_point, thousands_sep ) {
            var n = number, c = isNaN(decimals = Math.abs(decimals)) ? 2 : decimals;
            var d = dec_point == undefined ? "," : dec_point;
            var t = thousands_sep == undefined ? "." : thousands_sep, s = n < 0 ? "-" : "";
            var i = parseInt(n = Math.abs(+n || 0).toFixed(c)) + "", j = (j = i.length) > 3 ? j % 3 : 0;
                                    
            return s + (j ? i.substr(0, j) + t : "") + i.substr(j).replace(/(\d{3})(?=\d)/g, "$1" + t) + (c ? d + Math.abs(n - i).toFixed(c).slice(2) : "");
        },
        getTime(date){
            var arrTime = date.split("T");
            date = arrTime[0];
            var time = arrTime[1].split(".")[0];
            time = new moment(`${date} ${time}`);
            return time.fromNow();
        },
        redirect(search=null){
            search = search != null ? search : this.sort.search;
            if(this.sort.id_district===null ){
                this.$router.push({ 
                    name: "category", 
                    params: { 
                        province: this.sort.id_province, 
                        category: this.sort.id_category 
                    }, 
                    query: {
                        group: this.sort.group, 
                        search: search,
                        sortOption: this.sort.sortOption,
                        priceFrom: this.sort.priceFrom,
                        priceTo: this.sort.priceTo,
                        page:this.sort.page,
                        pageSize:this.sort.pageSize
                    }
                })
            }else{
                this.$router.push({ 
                    name: "route.category", 
                    params: { 
                        province: this.sort.id_province, 
                        district: this.sort.id_district,
                        category: this.sort.id_category 
                    }, 
                    query: {
                        group: this.sort.group,  
                        search: search,
                        sortOption: this.sort.sortOption,
                        priceFrom: this.sort.priceFrom,
                        priceTo: this.sort.priceTo,
                        page:this.sort.page,
                        pageSize:this.sort.pageSize
                    }
                })
            }
        },
        find(data, parent_id){
            var parent = [];
            data.forEach(e=>{
                if(e.parent_id == parent_id){
                    parent.push(e.id);
                    var result = this.find(data, e.id);
                    parent = parent.concat(result);
                }
            });
            return parent;
        },
        loadDataProduct(){
            var params = Object.assign({}, this.sort);
            axios.get(this.api + "/public/product",{
                params: params
            })
            .then(response=>{
                if(this.productFollow){
                    for(var i=0; i<response.data.product.length; i++){
                        var id = response.data.product[i].id;
                        var check = this.productFollow.find(e=>e.id_product==id);
                        check ? response.data.product[i].isProductFollow = true : response.data.product[i].isProductFollow = false;
                    }
                }
                this.product = response.data.product;
                this.pageCount = Math.ceil(response.data.count/params.pageSize);
                return axios.get(this.api + "/public/productpriority",{
                    params:params
                });
            })
            .then(response=>{
                if(this.productFollow){
                    for(var i=0; i<response.data.length; i++){
                        var id = response.data[i].id;
                        var check = this.productFollow.find(e=>e.id_product==id);
                        check ? response.data[i].isProductFollow = true : response.data[i].isProductFollow = false;
                    }
                }
                this.product = this.merge(this.product, response.data);
            })
        },
        group(value){
            if(this.sort.group != value ){
                this.sort.group = value;
                this.sort.page = 1;
                this.redirect();
            }
        },
        merge(data1, data2){
            if(!data1) return data2;
            if(!data2) return data1;
            var array = [];
            var count = data1.length > data2.length ? data1.length : data2.length;
            for(var i=0; i < count; i++){
                if(data1[i]) array.push(data1[i]);
                if(data2[i]){
                    data2[i].product_priority=true;
                    array.push(data2[i]);
                }
            }
            return array;
        }
    },
    created() {
        this.loadDataProduct();
        moment.locale("vi");
    }
};
</script>
