<template>
<div class="col-lg-4 col-md-4 col-sm-4 col-xs-4">
    <a v-on:click="show" :class="{ 'btn-loc': true, 'active': price}">lọc</a>
    <div :class="{ modal: true, fade: true, in: true ,show: menuLoc.status }">
        <div class="modal-dialog">
            <transition name="slide-fade">
                <div v-if="menuLoc.content" class="modal-content">
                    <form @submit.prevent="submitSort">
                        <div class="modal-header">
                            <button type="button" class="close" v-on:click="hide">
                            <span aria-hidden="true">&times;</span></button>
                            <h4 class="modal-title">Lọc</h4>
                        </div>
                        <div class="modal-body">
                            
                            <div class="price-sort">
                                <div class="form-group">
                                    <label v-if="!price" v-on:click="priceChoose" class="price-choose"><i class="fa fa-plus-square"></i> Chọn khoảng giá</label>
                                    <label v-if="price" v-on:click="priceRemove" class="price-remove"><i class="fa fa-minus-square"></i> Bỏ khoảng giá</label>
                                </div>
                                <transition name="priceChoose">
                                    <div v-show="price" class="form-group" style="position: relative;">
                                        <label>Từ (giá)</label>
                                        <input v-model="sort.priceFrom" name="priceFrom" v-validate="'min_value:0|max:15'" type="number" min="0" class="form-control input" placeholder="Vd: 10000" id="_priceFrom">
                                        <span v-if="sort.priceFrom > 0" class="format-price">{{ number_format(sort.priceFrom, 0, '.', ',') }} đ</span>
                                        <span class="error">{{ errors.first("priceFrom") }}</span>
                                    </div>
                                </transition>
                                <transition name="priceChoose">
                                    <div v-show="price" class="form-group" style="position: relative;">
                                        <label>Đến (giá)</label>
                                        <input v-model="sort.priceTo" name="priceTo" v-validate="'min_value:0|max:15'" type="number" min="0" class="form-control input" placeholder="Vd: 100000" id="_priceTo">
                                        <span v-if="sort.priceTo > 0" class="format-price">{{ number_format(sort.priceTo, 0, '.', ',') }} đ</span>
                                        <span class="error">{{ errors.first("priceTo") }}</span>
                                    </div>
                                </transition>
                            </div>
                            <div class="form-group">
                                <label>Sắp xêp</label>
                                <Select2 v-model="sortOption.myValue" :options="sortOption.myOptions" @change="changeSortOption($event)" class="input" id="_sortOption"/>
                            </div>
                        </div>
                        <div class="modal-footer">
                            <button type="button" class="btn btn-default pull-left" v-on:click="hide">Thoát</button>
                            <button type="submit" class="btn btn-primary">Tìm</button>
                        </div>
                    </form>
                </div>
            </transition>
        </div>
    </div>
</div>
</template>

<script>
import Select2 from 'v-select2-component';
import { Validator } from 'vee-validate';

export default {
    components: {Select2},
    data: function() {
        return {
            price: false,
            menuLoc: {
                status: false,
                content: false
            },
            sort:{
                id_province: this.$route.params.province,
                id_district: this.$route.params.district ? this.$route.params.district : null,
                id_category: this.$route.params.category,
                search: this.$route.query.search ? this.$route.query.search : null,
                priceFrom: this.$route.query.priceFrom ? this.$route.query.priceFrom : null,
                priceTo: this.$route.query.priceTo ? this.$route.query.priceTo : null,
                sortOption: this.$route.query.sortOption ? this.$route.query.sortOption : "time-new",
                group: this.$route.query.group ? this.$route.query.group : 'tat-ca',
                page: 1,
                pageSize: this.$store.state.pageSize
            },
            sortOption: {
                myValue: this.$route.query.sortOption ? this.$route.query.sortOption : "time-new",
                myOptions:  [
                    { id: "time-new", text: "Mặc định(mới nhất)"},
                    { id: "price-hight", text: "Giá giảm dần"},
                    { id: "price-low", text: "Giá tăng dần"}
                ] 
            },
            dictionary: {
                vi: {
                    custom: {
                        "priceFrom": {
                            decimal: "Vui lòng nhập giá bắt đầu !",
                            required: "Vui lòng nhập giá bắt đầu !",
                            min_value: "Giá tối thiểu bằng 0 !",
                            max: "Giá tối da 15 số theo chiều ngang !"
                        },
                        "priceTo": {
                            decimal: "Vui lòng nhập giá kết thúc !",
                            required: "Vui lòng nhập giá kết thúc !",
                            min_value: "Giá tối thiểu bằng 0 !",
                            max: "Giá tối da 15 số theo chiều ngang !"
                        }
                    }
                }
            }
        }
    },
    methods: {
        show () {
            this.menuLoc.status = true;
            this.menuLoc.content = true;
            document.querySelector("body").classList.add('modal-open');
        },
        hide () {
            this.menuLoc.content = false;
            document.querySelector("body").classList.remove('modal-open');
            setTimeout(() => {
                this.menuLoc.status = false;
            }, 400);
        },
        number_format( number, decimals, dec_point, thousands_sep ) {
            var n = number, c = isNaN(decimals = Math.abs(decimals)) ? 2 : decimals;
            var d = dec_point == undefined ? "," : dec_point;
            var t = thousands_sep == undefined ? "." : thousands_sep, s = n < 0 ? "-" : "";
            var i = parseInt(n = Math.abs(+n || 0).toFixed(c)) + "", j = (j = i.length) > 3 ? j % 3 : 0;
                                    
            return s + (j ? i.substr(0, j) + t : "") + i.substr(j).replace(/(\d{3})(?=\d)/g, "$1" + t) + (c ? d + Math.abs(n - i).toFixed(c).slice(2) : "");
        },
        submitSort(){
            this.$validator.validate().then(valid => {
                if (valid){
                    this.hide();
                    var query = this.$route.query;
                    if(query.priceFrom != this.sort.priceFrom || query.priceTo != this.sort.priceTo || query.sortOption != this.sort.sortOption)
                    this.redirect();
                }
            });
        },
        redirect(search=null){
            search = search != null ? search : this.sort.search;
            if(this.sort.id_district===null ){
                this.$router.push({ 
                    name: "category", 
                    params: { 
                        province: this.sort.id_province, 
                        category: this.sort.id_category 
                    }, 
                    query: {
                        group: this.sort.group, 
                        search: search,
                        sortOption: this.sort.sortOption,
                        priceFrom: this.sort.priceFrom,
                        priceTo: this.sort.priceTo,
                        page:this.sort.page,
                        pageSize:this.sort.pageSize
                    }
                })
            }else{
                this.$router.push({ 
                    name: "route.category", 
                    params: { 
                        province: this.sort.id_province, 
                        district: this.sort.id_district,
                        category: this.sort.id_category 
                    }, 
                    query: {
                        group: this.sort.group,  
                        search: search,
                        sortOption: this.sort.sortOption,
                        priceFrom: this.sort.priceFrom,
                        priceTo: this.sort.priceTo,
                        page:this.sort.page,
                        pageSize:this.sort.pageSize
                    }
                })
            }
        },
        priceChoose(){
            this.sort.priceFrom = null;
            this.sort.priceTo = null;
            this.price = true;
        },
        priceRemove(){
            this.sort.priceFrom = null;
            this.sort.priceTo = null;
            this.price = false;  
        },
        changeSortOption(val){
            this.sort.sortOption = val;
        }
    },
    created() {
        Validator.localize(this.dictionary);
        const validator = new Validator({ first_name: 'alpha' });
        validator.localize('vi');
        if(this.$route.query.priceFrom) this.price = true;
    }
};
</script>
<style>
.btn-loc.active{
    font-weight: 600;
    color: #FF9800;
}
.price-sort{
    margin: 10px 0px;
}
.price-sort .price-choose{
    color: green;
    cursor: pointer;
    width: 100% !important;
}
.price-sort .price-remove{
    color:red;
    cursor: pointer;
    width: 100% !important;
}
.priceChoose-enter-active {
    animation: priceChooseIn 0.4s forwards;
}
.priceChoose-leave-active {
    animation: priceChooseOut 0.4s forwards;
}
@keyframes priceChooseIn{
    0%{
        transform: translateY(-33px);
        height: 0px;
        opacity: 0;
    }
    
    100%{
        transform: translateY(0px);
        height: 34px;
        opacity: 1;
    }
}
@keyframes priceChooseOut{
    0%{
        transform: translateX(0px);
        height: 34px;
        opacity: 1;
    }
    
    100%{
        transform: translateY(-33px);
        height: 0px;
        opacity: 0;
    }
}
</style>