<template>
<div class="row">
    <div class="col-lg-4 col-md-4 col-sm-4 col-xs-4">
        <Select2 v-model="sort.id_province"
        :options="getProvinceByLv(0,{ id: 'toan-quoc', text: 'Toàn quốc' }, true)"
        @change="changeProvince($event)" />
    </div>
    <div class="col-lg-4 col-md-4 col-sm-4 col-xs-4">
        <div class="form-group">
            <Select2 v-model="sort.id_district"
            :options="sort.id_province=='toan-quoc' ? [] : getProvinceByLv(
                getProvinceByUrl(sort.id_province).id,
                { 
                    id: 'tat-ca-quan-huyen',
                    text: 'Tất cả quận huyện' 
                }, true)" 
            @change="changeDistrict($event)" />
        </div>
    </div>
    <div class="col-lg-4 col-md-4 col-sm-4 col-xs-4">
        <div class="form-group">
            <Select2 v-model="sort.id_category" 
            :options="getCategorySort(false)" 
            @change="changeCategory($event)" />
        </div>
    </div>
</div>
</template>

<script>
import Select2 from 'v-select2-component';
import {mapGetters} from 'vuex';

export default {
    components: {Select2},
    data: function() {
        return {
            sort:{
                id_province: this.$route.params.province,
                id_district: this.$route.params.district ? this.$route.params.district : null,
                id_category: this.$route.params.category,
                search: this.$route.query.search ? this.$route.query.search : null,
                priceFrom: this.$route.query.priceFrom ? this.$route.query.priceFrom : null,
                priceTo: this.$route.query.priceTo ? this.$route.query.priceTo : null,
                sortOption: this.$route.query.sortOption ? this.$route.query.sortOption : "time-new",
                group: this.$route.query.group ? this.$route.query.group : 'tat-ca',
                page: this.$route.query.page ? Number(this.$route.query.page) : 1,
                pageSize: this.$store.state.pageSize
            }
        }
    },
    methods: {
        changeProvince: function(val){
            this.page = 1;
			if(val != 'toan-quoc') this.sort.id_district = 'tat-ca-quan-huyen';
			else this.sort.id_district = null;
            this.redirect();
        },
        changeDistrict: function(){
            this.page = 1;
            this.redirect();
        },
        changeCategory: function(){
            this.page = 1;
            this.redirect();
        },
        redirect(search=null){
            search = search != null ? search : this.sort.search;
            if(this.sort.id_district===null ){
                this.$router.push({ 
                    name: "category", 
                    params: { 
                        province: this.sort.id_province, 
                        category: this.sort.id_category 
                    }, 
                    query: {
                        group: this.sort.group, 
                        search: search,
                        sortOption: this.sort.sortOption,
                        priceFrom: this.sort.priceFrom,
                        priceTo: this.sort.priceTo,
                        page:this.sort.page,
                        pageSize:this.sort.pageSize
                    }
                })
            }else{
                this.$router.push({ 
                    name: "route.category", 
                    params: { 
                        province: this.sort.id_province, 
                        district: this.sort.id_district,
                        category: this.sort.id_category 
                    }, 
                    query: {
                        group: this.sort.group,  
                        search: search,
                        sortOption: this.sort.sortOption,
                        priceFrom: this.sort.priceFrom,
                        priceTo: this.sort.priceTo,
                        page:this.sort.page,
                        pageSize:this.sort.pageSize
                    }
                })
            }
        }
    },
    computed:{
        ...mapGetters(['getProvinceByLv','getProvinceByUrl','getCategorySort'])
    }
};
</script>
